@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
@media (min-width: 993px) {
  #heating .heating-container {
    max-width: 1610px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
  }
}
#heating .heating-items {
  width: 100%;
  position: relative;
  padding-bottom: 100px;
}
@media (max-width: 992px) {
  #heating .heating-items {
    padding-bottom: 60px;
    overflow: hidden;
  }
}
@media (max-width: 576px) {
  #heating .heating-items {
    padding: 0 10px 60px;
  }
}
@media (min-width: 993px) {
  #heating .swiper-wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
  }
}
@media (min-width: 993px) and (max-width: 1280px) {
  #heating .swiper-wrapper {
    gap: 20px;
  }
}
@media (min-width: 993px) and (max-width: 1024px) {
  #heating .swiper-wrapper {
    gap: 15px;
  }
}
#heating .heating-navigation {
  display: none;
}
@media (max-width: 992px) {
  #heating .heating-navigation {
    display: flex;
    position: absolute;
    bottom: 84px;
    gap: 35px;
    left: 50%;
    z-index: 5;
    transform: translateX(-50%);
  }
}