
@import "../../scss/_global.scss";

#heating {
  .heating-container {
    @media (min-width: 993px) {
      max-width: 1610px;
      width: 100%;
      margin: 0 auto;
      padding: 0 20px;
    }
  }

  .heating-items {
    width: 100%;
    position: relative;

    padding-bottom: 100px;

    @media (max-width: 992px) {
      padding-bottom: 60px;
      overflow: hidden;
    }

    @media (max-width: 576px) {
      padding: 0 10px 60px;
    }
  }

  .swiper-wrapper {
    @media (min-width: 993px) {
      display: grid;
      grid-template-columns: repeat(3, 1fr);

      @include gapWrapper();
    }
  }

  .heating-navigation {
    display: none;
    @media (max-width: 992px) {
      display: flex;
      position: absolute;
      bottom: 84px;
      gap: 35px;
      left: 50%;
      z-index: 5;
      transform: translateX(-50%);
    }
  }
}
